/**
 * 辅助线相关
 */

const line = {
    state: {
        list: []
    },
    getters: {
        getPosition(state) {
            if (state.length < 1) return []
            let pos = {
                x: [],
                y: []
            };
            state.list.map(it => {
                pos.x.push(parseFloat(it.left))
                pos.y.push(parseFloat(it.top))
            })
            return pos;
        },

    }
}

export default line