<template>
   <div id="app">
      <nav-header @showPreview="showPreview"></nav-header>
      <nav-side></nav-side>
      <edit-panel></edit-panel>
      <div class="content">
         <scaleplate></scaleplate>
         <h5-canvas>
            <node-wrap></node-wrap>
         </h5-canvas>
      </div>
      <preview-dialog ref="preview" @hidePreview="hidePreview" :visible="visible"></preview-dialog>
   </div>
</template>

<script>
import { installPlugins } from "./core/plugins/install";

export default {
   components: {
      "nav-header": () => import("./components/layout/nav-header/nav-header"),
      "nav-side": () => import("./components/layout/nav-side/nav-side"),
      "edit-panel": () => import("./components/layout/edit-panel/edit-panel"),
      scaleplate: () => import("./components/layout/scaleplate/scaleplate"),
      "h5-canvas": () => import("./components/layout/h5-canvas/h5-canvas"),
      "node-wrap": () => import("./components/layout/node-wrap/node-wrap"),
      "preview-dialog": () =>
         import("./components/layout/preview-dialog/preview-dialog")
   },
   created() {
      // 安装所有插件
      installPlugins();
   },
   data() {
      return {
         visible: false
      };
   },

   methods: {
      showPreview() {
         this.$refs.preview.showDialog();
         this.visible = true;
         //  this.$refs.preview.showPreview();
      },
      hidePreview() {
         this.visible = false;
      }
   }
};
</script>

