/**
 * install
 * 功能：安装所有的自定义组件
 */

import Vue from 'vue'

// 组件
import grgPic from '../../components/plugins/grg-pic/grg-pic.vue'
import grgText from '../../components/plugins/grg-text/grg-text'

import css from '../../components/plugins/grg-pic/style.less'
console.log(css)
// 编辑器




const pluginList = [{
        name: '图片',
        component: grgPic,
        tagName: grgPic.name,
    },
    {
        name: '文本',
        component: grgText,
        tagName: grgText.name,
    }
]


export const installPlugins = () => {
    pluginList.forEach((it) => {
        Vue.component(it.tagName, it.component)
    })
}